export default {
	data() {
		return {
			isPayQR:"",
			shopObj:{},
			textInfo: {},
			invite: "",//13327313408
			isDown: false, //是否显示微信收款等下拉
		}
	},
	methods: {
		limit(limit){
			var size = "";
			size = (limit/(1024 * 1024)).toFixed(2);//转MB
			var sizeStr = size + "";                        //转成字符串
			var index = sizeStr.indexOf(".");                    //获取小数点处的索引
			var dou = sizeStr.substr(index + 1 ,2)            //获取小数点后两位的值
			if(dou == "00"){                                //判断后两位是否为00，如果是则删除00
				return sizeStr.substring(0, index) + sizeStr.substr(index + 3, 2)
			}
			return size;
		},
		//更换店铺图片
		changeImg(){
			let that=this;
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				success: function (res) {
					if(that.limit(res.tempFiles[0].size)>3){
						uni.showToast({
							title:"图片太大",
							icon:"none"
						})
						return
					}
			        that.$ajax({
						uploadFile:true,
						formData:{
							'fileName':"/marchant/",
						},
						filePath:res.tempFilePaths[0],
						success(d){
							that.uploadImg(d)
							that.shopObj.disPlayImg=d;
							uni.setStorage({
								key:"shopObj",
								data:JSON.stringify(that.shopObj)
							})
						}
					})
					
			
				},
			});
		},
		uploadImg(src){
			let that=this;
			that.$ajax({
				url:"/system_manager/setLogo",
				data:{
					displayImg: src,
					shopId:that.shopObj.shopId
				},success(d){
					console.log(d);
				}
			})
		},
		urlCode(e){
			let type=parseInt(Math.random()*2,10);
			uni.navigateTo({
				url: e.currentTarget.dataset.url+"?type="+type
			})
		},
		url(e) {
			uni.navigateTo({
				url: e.currentTarget.dataset.url
			})
		},
		changeDown(e) {
			this.isDown = !this.isDown
		},
		isExistsOfflinePayQR(){
			let that=this;
			that.$ajax({
				url:"/merchant/isExistsOfflinePayQR",
				data:{
					merchantId:that.shopObj.merchantId
				},
				success(d){
					that.isPayQR=d;
				}
			})
		}
	},
	onShow() {
		let that=this;	
		that.isDown = false;
		that.$ajax({
			url:"/token/get",
			method:"POST",
			data:{
				merchantId:that.shopObj.merchantId
			},success(d){
				uni.setStorage({
					key:"Token",
					data:d
				})
				that.$ajax({
					url:"/home/index",
					data:{
						level: that.shopObj.level,
						merchantId: that.shopObj.merchantId,
						shopId: that.shopObj.shopId
					},success(d){		
						that.textInfo=d;
						that.isExistsOfflinePayQR()
					}
				})
			}
		})
	},
	onLoad() {
		let that=this;
		uni.getStorage({
			key:"shopObj",
			success(res){
				console.log(res)
				that.shopObj=JSON.parse(res.data);
				console.log(that.shopObj)
				if (!that.shopObj.shopId) {
					uni.showModal({
						title: '未登录',
						content: '登录失效，需要登录后才能继续',
						/**
						 * 如果需要强制登录，不显示取消按钮
						 */
						showCancel: !that.forcedLogin,
						success: (res) => {
							if (res.confirm) {
								// /**
								//  * 如果需要强制登录，使用reLaunch方式
								//  */
								if (that.forcedLogin) {
									uni.reLaunch({
										url: '../login/login'
									});
								} else {
									uni.reLaunch({
										url: '../login/login'
									});
								}
							}
						}
					});
				}
			}
		})
	}
}
